<template>
    <transition name="fade">
        <div class="sc-toast-wrapper" v-if="exist">
            <div class="sc-toast-box" :class="'sc-toast-'+type+ ' '+position" :style="{zIndex: zIndex+1}">
                <div class="sc-toast-icon" v-if="type!=='text'"></div>
                <div class="sc-toast-message">{{message}}</div>
            </div>
            <div class="sc-mask" :style="{zIndex: zIndex}" v-if="shadow"></div>
        </div>
    </transition>
</template>
<script>
export default {
  data() {
    return {
      zIndex: null,
      exist: false,
      shadow: false,
      position: '',
      type: '',
      message: ''
    }
  }
}
</script>
<style>
.sc-toast-box{
    position: fixed;
    bottom: 50%;
    left: 50%;
    transform: translate3d(-50%,50%,0);
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    border-radius: 2px;
}
.sc-toast-box .sc-toast-icon{
    width: 60px;
    height: 60px;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: cover;
}
.sc-toast-text{
    padding: 0.3rem;
    border-radius: 5px;
}
.sc-toast-loading {
    padding: 10px 20px 10px;
}
 .sc-toast-success {
    padding: 10px 20px 10px;
 }
 .sc-toast-text.bottom{
     bottom: 30px;
     transform: translate3d(-50%,0,0);

 }
.sc-toast-success .sc-toast-icon{
    background-image: url("data:image/png;base64,");
}
.sc-toast-loading .sc-toast-icon{
    background-image: url("data:image/gif;base64,");
}
.sc-toast-message{
    text-align: center;
    font-size: 0.25rem;
    line-height: 20px;
}

</style>



